.apex-home {
  color: white;
  min-height: 100vh;

  .document {
    padding: 8rem 0;

    .top-half,
    .bottom-half {
      cursor: pointer;
      overflow: hidden;
      background-color: rgba(255, 255, 255, 0.08);
      height: 24rem;
      margin: 2rem 0;

      .text {
        padding: 2rem;
      }

      h1 {
        margin: 0;
      }

      ul {
        margin: 0;

        li {
          margin: 1rem 0;
        }
      }
      img {
        height: 100%;
        width: 50%;
        object-fit: cover;
      }
    }

  //   .profile {
  //     img {
  //         width: 100%; // Adjust width as needed
  //         margin-bottom: 10px; // Adjust the value as needed to create the desired space
  //     }
  //     text-align: center;
  //     .name {
  //         margin-top: 10px; // Adjust the value as needed to create the desired space
  //         text-align: center;
  //     }
  // }

    .top-half {
      // clip-path: polygon(0 0, 100% 0, 100% calc(100% - 4rem), 0 100%);
      border-top-left-radius: 2rem;
      border-top-right-radius: 2rem;
      margin-right: 4rem;

      img {
        clip-path: polygon(1rem 0, 100% 0, 100% 100%, 0 100%);
      }
    }

    .bottom-half {
      // clip-path: polygon(0 4rem, 100% 0, 100% 100%, 0 100%);
      border-bottom-right-radius: 2rem;
      border-bottom-left-radius: 2rem;
      margin-left: 4rem;

      img {
        clip-path: polygon(0 0, 100% 0, calc(100% - 1rem) 100%, 0 100%);
      }

      ul {
        direction: rtl;
      }
    }
    .fade-image {
      position: relative;
      width: 50%; // Adjust as needed
      height: 100%;

      .fade-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.apex-coding,
.apex-consulting {
  color: white;
  min-height: 100vh;
  padding: 8rem 0 2rem;

  .document {
    background-color: rgba(255, 255, 255, 0.08);
    border-radius: 2rem;
    overflow: hidden;

    > *:not(:first-child) {
      margin-top: 2rem;
    }

    .explanation {
      padding: 0 2rem;
    }

    .text {
      padding: 2rem;
    }

    .h0 {
      letter-spacing: 1px;
      margin: 0;
      text-shadow: 0 0.25rem 0.5rem black;
      cursor: pointer;
      background-color: rgba(255, 255, 255, 0.08);
      padding: 2rem;

      .back {
        position: absolute;
        left: 2rem;
        padding: 0 1rem;
        border-radius: 1rem;
      }
    }

    .list {
      padding: 0 2rem;
    }

    ul {
      margin: 0;
      li {
        margin: 1rem 0;
      }
    }

    iframe {
      width: 100%; /* Adjusted for responsiveness */
      aspect-ratio: 3/2;
      border: none; /* Ensure no border */
      margin-top: 1rem; /* Added margin top for spacing */
    }

    .click-to {
      cursor: pointer;
      background-color: rgba(255, 255, 255, 0.08);
      padding: 2rem;
      border-bottom-left-radius: 1rem;
      border-bottom-right-radius: 1rem;
    }

    img {
      width: 100%; /* Adjusted for responsiveness */
      max-width: 18rem; /* Added max-width to limit size on larger screens */
      height: auto; /* Maintain aspect ratio */
      border-radius: 2rem;
      margin-top: 1rem; /* Added margin top for spacing */
    }

    .swiper-button-prev {
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2027%2044'%3E%3Cpath%20d%3D'M0%2C22L22%2C0l2.1%2C2.1L4.2%2C22l19.9%2C19.9L22%2C44L0%2C22L0%2C22L0%2C22z'%20fill%3D'%234c71ae'%2F%3E%3C%2Fsvg%3E") !important;
    }
  }

  @media (max-width: 768px) {
    .document {
      padding: 1rem; /* Reduce padding on smaller screens */
    }

    .h0 {
      font-size: 2rem; /* Decrease font size on smaller screens */
    }

    .list {
      flex-direction: column; /* Stack list items vertically on smaller screens */
    }

    iframe {
      width: 100%; /* Make iframe full width on smaller screens */
    }

    img {
      width: 100%; /* Make images full width on smaller screens */
      max-width: none; /* Remove max-width constraint */
    }
  }
}


